<template>
  <center id="mainDiv">
    <div id="left">left</div>

    <div id="center">
      <div>
        <!-- v-model input에서 값을 받아올 이름 지정 --> 
        <input type="text" v-model="user.uname">
        <input type="text" v-model="user.money">
        <button type="button" @click="pushData">로그인</button>
        <button type="button" @click="getTable">더보기</button>
        
      </div>

      {{user.uname}} | {{user.money}}
      <hr>

      <table>
        <tr><td>번호</td><td>이름</td></tr>

        <tr v-for="movie in movies" v-bind:key="movie.name">
          <td>{{movie.uid}}</td><td>{{movie.uname}}</td>
        </tr>
      </table>
    </div>

    <div id="right">right</div>

  </center>
</template>

<script>
export default {
  data () {
    return {
      user:{
        uname:"",
        money:""
      },
      movies: [],
    }
  },
  methods:{
    pushData(){
      this.$http.post('/api/insertUser',{
        user: this.user
      }).then((res) => {
          console.log(res)
        }).catch((err) => {
          console.log('sibal'+err);
        })
    },
    getTable(){
      this.$http.get('/api/userList')
        .then((res) => {
          console.log(res.data)
          this.movies = res.data
        })
    }
  }
}
</script>

<style>
#mainDiv{
  width: 1450px;
  background: black;
  display: flex;
  margin: 0 auto;
}
#left{
  background: red;
  flex: 1;
}
#center{
  background: #FBCEB1;
  flex: 4;
}
#right{
  background:green;
  flex: 1;
}
</style>