import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Offer from '../views/Offer.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: "Home"
    }
  },
  {
    path: '/offer',
    name: 'Offer',
    component: Offer
  }
]

const router = new VueRouter({
  routes
})

export default router
